import MyPagination from "../../../components/MyPagination";
import mixins from "../../../utils/mixins";
import {common} from "../../../utils/api/url";
import {levelText, MessageError, MessageSuccess, MessageWarning, inArray} from "../../../utils/utils";
import LayoutTb from "../../../components/Layout/LayoutTb";
import MySelect from "../../../components/MySelect";
import MyInputButton from "../../../components/search/MyInputButton";
import DialogNormal from "../../../components/utils/dialog/DialogNormal";
import DrawerLayoutRight from "../../../components/utils/dialog/DrawerLayoutRight";
import MyDeviceUseType from "../../../components/utils/status/MyDeviceUseType";
import doorManageValidater from "../../../utils/validater/doorManageValidater";
export default {
  mixins: [mixins, doorManageValidater],
  components: {MyPagination,LayoutTb,MySelect,MyInputButton,DialogNormal,DrawerLayoutRight,MyDeviceUseType},
  data(){
    return {
      tableData: [],
      tableDeviceData: [],
      searchDate: [],
      typeList: [],
      searchKey: '',
      visible: false,
      modalVisible: false,
      modalConfVisible: false,
      modalConfDeviceVisible: false,
      dialogLoading: false,
      drawerLoading: false,
      visibleConfim: false,
      drawerVisible: false,
      loading: false,
      perLoading: false,
      picLoading: false,
      mutiDeleteLoading: false,
      mutiUnbindLoading: false,
      mutiResetLoading: false,
      modalAddVisible: false,
      deviceLoading: false,
      searchOnline: '',
      config: {},
      subTitle: '',
      deleteSetTitle: '确认需要删除该信息？',
      addTitle: '添加设备',
      status: '',
      deviceList: [],
      deviceAddList: [],
      deviceControlList: [],
      deviceControlOprList: [],
      tableDeviceControlData: [],
      deviceSelData: [],
      deviceExtraList: [],
      searchIndex: '',
      id: '',
      filterOnlineStatusText: '',
      form: {
        id: '',
        sn: '',
        ip: '',
        comment: '',
        name: '',
        collisionAvoidanceTime: '',
        collisionAvoidance: true
      },
      formSet: {

      },
      formConf: {
        accessControlServerIp: '',
        accessControlServerPort: ''
      },
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        keyWord: this.searchKey,
        type: 5,
        online: this.searchOnline
      };
      this.$axios.get(common.dormaccess_control_device_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    async initConfig(row){
      let params = {
        accessControlSn: row.sn
      };
      let typeList = [];
      let deviceList = [];
      await this.$axios.get(common.dormaccess_control_device_control_type).then(res => {
        if (res.data.data){
          typeList = res.data.data;
        }
      });
      await this.$axios.get(common.dormaccess_control_device_control_device).then(res => {
        if (res.data.data){
          deviceList = res.data.data;
        }
      });
      await this.$axios.get(common.dormaccess_control_device_control_list, {params: params}).then(res => {
        if (res.data.data){
          for (let i = 0; i < res.data.data.length; i++){
            res.data.data[i]['typeList'] = typeList;
            res.data.data[i]['deviceFaceList'] = deviceList;
            res.data.data[i]['recModesArr'] = res.data.data[i].rec_modes != null ? res.data.data[i].rec_modes.split(",") : [];
            res.data.data[i]['deviceListArr'] = res.data.data[i].deviceList != null ? res.data.data[i].deviceList : [];

            if (res.data.data[i].deviceList != null && res.data.data[i].deviceList.length > 0){
              this.deviceSelData = this.deviceSelData.concat(res.data.data[i].deviceList);
              this.deviceSelData = [...new Set(this.deviceSelData)];
              this.deviceSelData = this.deviceSelData;
            }
          }
          this.tableDeviceControlData = res.data.data;
        }
      });
    },
    initDeviceExtra(deviceArr){
      let arr = [];
      this.$axios.get(common.dormaccess_control_device_control_device).then(res => {
        if (res.data.data){
          for (let i = 0; i < res.data.data.length; i++){
            let sel = inArray(res.data.data[i].sn, this.deviceSelData);
            let selExist = inArray(res.data.data[i].sn, deviceArr);
            if (sel == -1){
              arr.push(res.data.data[i]);
            }else if (selExist != -1){
              arr.push(res.data.data[i]);
            }
          }
          this.deviceExtraList = arr;
        }
      });
    },
    initDevice(){
      let params = {
        page: 1,
        num: 9999,
        time: 5
      };
      this.deviceLoading = true;
      this.$axios.get(common.dormaccess_control_device_search, {params: params, loading: false}).then(res => {
        if (res.data.data && res.data.data.length > 0){
          for (let i = 0; i < res.data.data.length; i++){
            res.data.data[i]['doorNoSet'] = [];
          }
          this.tableDeviceData = res.data.data;
          this.addTitle =  this.$t("添加设备") + "(" + this.$t("已发现") + this.tableDeviceData.length + this.$t("台") + "," + this.$t("已选择 0 台") + ")" ;
        }
        this.deviceLoading = false;
      });
    },
    initSetting(row){
      this.$axios.get(common.device_get).then(res => {
        if (res.data.data){
          this.formConf = {
            accessControlServerIp: res.data.data.accessControlServerIp,
            accessControlServerPort: res.data.data.accessControlServerPort,
          };
        }
      });
    },
    confInfo(){
      this.initSetting();
      this.modalConfDeviceVisible = true;
    },
    setCallbackUrl(){
      let params = {
        accessControlServerIp: this.formConf.accessControlServerIp,
        accessControlServerPort: this.formConf.accessControlServerPort,
      };
      params = this.$qs.stringify(params);
      this.$axios.post(common.device_door_down, params, {loading: false}).then(res => {
        if (res.data.code == 200){

        }else {
          MessageWarning(res.data.desc);
        }
      });
    },
    addInfo(){
      this.initDevice();
      this.modalAddVisible = true;
    },
    editInfo(row){
      this.form = {
        id: '',
        sn: row.sn,
        ip: row.ip,
        comment: row.comment,
        name: row.name
      };

      this.$set(this.form, 'collisionAvoidanceTime', ''+row.collisionAvoidanceTime);
      this.$set(this.form, 'collisionAvoidance', row.collisionAvoidance);
      this.modalVisible = true;
    },
    setInfo(row){
      this.initConfig(row);
      this.modalConfVisible = true;
    },
    deleteInfo(row){
      this.id = row.sn;
      this.subTitle = row.name ? row.name : row.sn;
      this.visibleConfim = true;
    },
    deleteMutiInfo(){
      let arr = [];
      for (let i = 0; i < this.deviceList.length; i++){
        arr.push(this.deviceList[i].sn);
      }
      let params = {
        snList: arr.join()
      };
      params = this.$qs.stringify(params);
      this.mutiDeleteLoading = true;
      this.$axios.post(common.dormaccess_control_device_del, params, {loading: false}).then(res => {
        if (res.data.code == 200){
          this.init();
          this.deviceList = [];
          this.$refs.refTable.clearSelection();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.mutiDeleteLoading = false;
      });
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    search(data){
      this.page = 1;
      this.searchKey = data.input;
      this.init();
    },
    okDialog(){
      let url = "";
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            ip: this.form.ip,
            sn: this.form.sn,
            name: this.form.name,
            comment: this.form.comment,
            collisionAvoidanceTime:  this.form.collisionAvoidanceTime,
            collisionAvoidance: this.form.collisionAvoidance
          };

          params = this.$qs.stringify(params);
          this.$axios.post(common.dormaccess_control_device_edit, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.modalVisible = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    okDeviceDialog(){
      let url = "";
      let arr = [];
      let errNum = 0;
      for (let i = 0; i < this.deviceAddList.length; i++){
        arr.push({
          ip: this.deviceAddList[i].ip,
          manufacturer: this.deviceAddList[i].manufacturer,
          port: this.deviceAddList[i].port,
          sn: this.deviceAddList[i].sn,
          type: this.deviceAddList[i].type,
        });
        if (this.deviceAddList[i].doorNoSet.length == 0){
          errNum++;
        }
        arr[i]['doorNoSet'] = this.deviceAddList[i].doorNoSet
      }
      if (errNum > 0){
        MessageWarning(this.$t("选中的设备请至少选择一个接口"));
        return;
      }
      let params = {
        deviceList: arr
      };

      this.dialogLoading = true;
      this.$axios.post(common.dormaccess_control_device_add, JSON.stringify(params), {dataType: 'stringfy'}).then(res => {
        if (res.data.code == 200){
          this.modalAddVisible = false;
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.dialogLoading = false;
      });
    },
    okConfDialog(){
      let url = "";
      let errNum = 0;
      let arr = [];
      for (let i = 0; i < this.tableDeviceControlData.length; i++){
        arr.push({
          sn: this.tableDeviceControlData[i].sn,
          autoOpen: this.tableDeviceControlData[i].auto_open,
          delayTime: this.tableDeviceControlData[i].delay_time,
          recModes: this.tableDeviceControlData[i].recModesArr.join(),
          snList: this.tableDeviceControlData[i].deviceListArr,
        });
      }
      let params = {
        deviceList: arr
      };
      this.dialogLoading = true;
      this.$axios.post(common.dormaccess_control_device_control_edit, JSON.stringify(params), {dataType: 'stringfy'}).then(res => {
        if (res.data.code == 200){
          this.modalConfVisible = false;
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.dialogLoading = false;
      });
    },
    closeDialog(event){
      this.form = {
        id: '',
        sn: '',
        ip: '',
        comment: '',
        name: '',
        collisionAvoidanceTime:  "",
        collisionAvoidance: true
      };
      this.deviceAddList = [];
      this.deviceSelData = [];
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
      if (this.$refs.refDeviceTable){
        this.$refs.refDeviceTable.clearSelection();
      }
    },
    cancelDialog(){
      this.modalAddVisible = false;
      this.modalVisible = false;
      this.modalConfVisible = false;
      this.modalConfDeviceVisible = false;
    },
    closeDrawerDialog(event){
      this.formSet = {

      };
      this.drawerVisible = event;
    },
    cancelDrawDialog(){
      this.drawerVisible = false;
    },
    okDrawDialog(event){
      this.drawerLoading = true;
      let params = {};
      params = this.formSet;
      params = this.$qs.stringify(params);
      this.$axios.post(common.wo_pad_config_set, params).then(res => {
        if (res.data.code == 200){
          this.drawerVisible = false;
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.drawerLoading = false;
      });
    },
    handleSelect(data, type){
      switch (type) {
        case 1:
          this.formSet.recRank = data;
          break;
        case 2:
          this.formSet.mutiplayerDetetion = data;
          break;
        case 3:
          this.form.use = data;
          break
      }
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let url = "";
      let params = {
        sn: this.id
      };
      url = common.dormaccess_control_device_del_only;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    },
    getRowKeys(row) {
      return row.sn;
    },
    getRowDeviceKeys(row){
      return row.sn;
    },
    handleSelectionChange(data){
      this.deviceList = data;
    },
    handleSelectionDeviceChange(data){
      this.addTitle = this.$t("添加设备") + "(" + this.$t("已发现") + this.tableDeviceData.length + this.$t("台") + "," + this.$t("已选择") + data.length + this.$t("台") + ")";
      this.deviceAddList = data;
    },
    handleChangeBox(data, row, index){
      this.tableDeviceData[index]['doorNoSet'] = data;
    },
    handleChangeTypeBox(data, row, index){
      this.tableDeviceControlData[index]['recModesArr'] = data;
    },
    handleChangeDeviceBox(data, row, index){
      this.deviceSelData = this.deviceSelData.concat(data);
      this.deviceSelData = [...new Set(this.deviceSelData)];
      this.deviceSelData = this.deviceSelData;
      this.tableDeviceControlData[index]['deviceListArr'] = data;
    },
    handleChangeOnlyDeviceBox(event, item){
      if (event == false){
        let sel = inArray(item.sn, this.deviceSelData);
        if (sel != -1){
          this.deviceSelData.splice(sel, 1);
        }
      }
    },
    fliterTable(value, row, column){
      for (let item in value){
        if (item == 'status'){
          this.filterOnlineStatusText = '';
          this.searchOnline = value[item][0];
          for (let i = 0; i < this.filterOnlineStatus.length; i++){
            if (this.searchOnline == this.filterOnlineStatus[i].value){
              this.filterOnlineStatusText = this.filterOnlineStatus[i].text;
            }
          }
        }
      }
      this.page = 1;
      this.init();
    },
    handleChange(type){

    },
    handleChangeEnable(data, row, index){
      this.tableDeviceControlData[index]['auto_open'] = data;
    },
    inArrayInfo(search, arr){
      return inArray(search, arr);
    },
    changeCollisionAvoidance(event, data){
      this.$set(this.form, 'collisionAvoidance', data);
    },
    okConfDeviceDialog(){
      let url = "";
      this.$refs['formConf'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            accessControlServerIp: this.formConf.accessControlServerIp,
            accessControlServerPort: this.formConf.accessControlServerPort,
          };

          params = this.$qs.stringify(params);
          this.$axios.post(common.device_set, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.modalConfDeviceVisible = false;
              this.setCallbackUrl();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    }
  }
}
